// accordeon

.accordeon {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.accordeon__item {
  width: 100%;
  margin-bottom: 6px;

  &:last-child {
    margin-bottom: 0;
  }
}

.accordeon__term {
  width: 100%;
}

.accordeon__button {
  position: relative;
  z-index: 101;
  width: 100%;
  min-width: 120px;
  .mp-reset();
  padding: 2px;
}

.accordeon__def {
  height: 0;
  opacity: 0;
  font-size: 0.85em;
  transition: all 0.2s linear;
  border: 1px solid violet;
  border-top: none;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

.accordeon__def--show {
  opacity: 1;
}

.accordeon__def-content {
  visibility: hidden;
  padding: 0.5em;
  transform: translateY(-32px);
  transition-property: visibility, transform;
  transition-duration: 0.2s, 0.4s;
  transition-timing-function: linear, ease-in;
}

.accordeon__def--show .accordeon__def-content {
  visibility: visible;
  transform: translateY(0);
}
