// about-me

.about-me__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.about-me__picture {
  margin-bottom: 2.25em;
  border-radius: 20px;
}

.about-me__info {
  margin-bottom: 1em;
  word-break: normal;

  p {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }
}

.about-me__features {
  li {
    margin-bottom: 0.3em;
  }

  li:last-child {
    margin-bottom: 0;
  }
}
