// modal

.modal {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(4px);
  opacity: 0;
  transition-property: opacity, z-index;
  transition-duration: 0.3s;
  transition-timing-function: linear;

  @media (min-width: 840px) {
    overflow-y: scroll;
  }
}

.modal--show {
  z-index: 100500;
  opacity: 1;
  animation-name: blur;
  animation-duration: 0.3s;
  animation-timing-function: linear;
}

.modal__content {
  width: 100%;
  max-width: 940px;
  min-height: 100%;
  margin-left: auto;
  padding-top: 2.5rem;
  padding-right: 2.5%;
  padding-bottom: 2.5rem;
  padding-left: 2.5%;
  background-color: transparent;
  opacity: 0;
  transition-property: opacity, background-color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in;
}

.modal__content--show {
  opacity: 1;
  background-color: @basic-white;
  animation-name: blur;
  animation-duration: 0.3s;
  animation-timing-function: ease-in;
}

.modal__close {
  position: absolute;
  top: calc(2.5rem + 7px);
  right: 2.5%;
  width: 25px;
  height: 25px;
  .mp-reset();
  background-color: transparent;
  border: none;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 11.5px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: @basic-black;
    transform: rotate(-45deg);
  }

  &::after {
    top: auto;
    bottom: 11.5px;
    transform: rotate(45deg);
  }
}

@keyframes blur {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
