// site-title

.site-title {
  display: block;
  font-size: 1.445em;
  color: @page-black;
  text-align: center;

  img {
    display: none;
  }

  @media (min-width: 840px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    img {
      display: block;
      width: 45px;
      height: 45px;
      border-radius: 100%;
      box-shadow: 0 8px 15px 0 rgba(0, 0, 0, 0.2), 0 0 0 4px #ffffff;
    }

    span {
      padding-left: 0.4em;
    }
  }

  @media (min-width: 1200px) {
    img {
      width: 110px;
      height: 110px;
      box-shadow: 0 8px 15px 0 rgba(0, 0, 0, 0.2), 0 0 0 11px #f2f2f2,
        0 0 0 24px #fafafa;
    }

    span {
      padding-left: 0.9em;
    }
  }
}
