// info

.info {
  color: @page-black;
}

.info__job {
  margin-bottom: 1.55em;

  @media (min-width: 840px) {
    margin-bottom: 2em;
  }
}

.info__title {
  padding-right: 25px;
  font-size: 1.5em;
  font-weight: 400;
}

.info__implement {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.info__implement-text {
  margin-bottom: 1.55em;

  @media (min-width: 840px) {
    margin-right: 0.4em;
    margin-bottom: 0;
  }
}

.info__tech-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  .mp-reset();
  list-style: none;

  @media (min-width: 840px) {
    width: auto;
  }
}

.info__tech-icon {
  display: flex;
  align-items: center;
  justify-content: center;

  @media (min-width: 840px) {
    margin-right: 0.4em;

    &:last-child {
      margin-right: 0;
    }
  }
}

.info__content {
  @media (min-width: 600px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .info__desc {
      width: 55%;
    }

    .info__meta {
      width: 40%;
      padding-top: 4px;
      font-size: 0.7em;
    }
  }
}

.info__desc {
  margin-bottom: 1.5em;
  font-size: 1.058em;
}

.info__meta {
  margin-bottom: 2.5em;
  font-size: 0.7em;
  line-height: 1.8;

  @media (min-width: 840px) {
    margin-bottom: 2.5em;
  }
}

.info__meta-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info__meta-wrap dt {
  font-weight: 700;
}

.info__meta-note {
  font-weight: 400;
}

.info__link {
  color: @page-pink;
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    color: @page-black;
  }
}

.info__diff[data-diff="easy"] {
  color: green;
}

.info__diff[data-diff="middle"] {
  color: orange;
}

.info__diff[data-diff="heavy"] {
  color: tomato;
}
