// header
.header {
  position: relative;
  min-height: 150px;
  padding-top: 2.5em;
  padding-right: 1.5rem;
  padding-bottom: 1.6em;
  padding-left: 1.5rem;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: @basic-light;
  }

  .site-title {
    margin-bottom: 0.75em;
  }

  @media (min-width: 600px) {
    font-size: 1.125em;
  }

  @media (min-width: 840px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-height: auto;
    padding-top: 2em;
    padding-right: 3rem;
    padding-bottom: 2em;
    padding-left: 3rem;

    .site-title {
      margin-bottom: 0;
    }

    .site-nav {
      margin-left: auto;
    }

    .site-list li {
      margin-right: 1em;
    }

    .site-list li:last-child {
      margin-right: 0;
    }
  }

  @media (min-width: 1200px) {
    font-size: 1.5em;
  }
}
