// skill

.skill {
  padding: 0.5em;
  margin-bottom: 1em;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.3);

  &:last-child {
    margin-bottom: 0;
  }
}

.skill__title {
  margin-bottom: 0.7em;
  padding-bottom: 0.2em;
  font-weight: 400;
  letter-spacing: 0.12em;
  border-bottom: 1px solid @basic-light;
}

.skill__list {
  display: flex;
  flex-wrap: wrap;
  .mp-reset();
  list-style: none;
}

.skill__icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 1.5em;
  padding: 0.2em;

  &:last-child {
    margin-right: 0;
  }
}

.skill__desc {
  color: @page-pink;
  font-size: 0.75em;
}
