// project

.project {
  overflow: hidden;

  @media (min-width: 600px) {
    width: 50%;
  }

  @media (min-width: 1200px) {
    width: 33.33%;
  }

  @media (min-width: 1800px) {
    width: 25%;
  }
}

.project__link {
  display: block;
  position: relative;

  img {
    width: 100%;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }

  &:focus::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border: 5px solid @page-pink;
  }
}

.project__icon {
  position: absolute;
  z-index: 101;
  top: 50%;
  left: 50%;
  width: 250px;
  height: 250px;
  transform: translate(-50%, -1500px);
  transition-property: transform;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  @media (min-width: 600px) {
    width: 150px;
    height: 150px;
  }

  @media (min-width: 840px) {
    width: 200px;
    height: 200px;
  }

  @media (min-width: 1800px) {
    width: 250px;
    height: 250px;
  }
}

.project__link:hover .project__icon {
  transform: translate(-50%, -50%);
}

.project__link:hover img {
  opacity: 0.3;
}
