// site-list

.site-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  .mp-reset();
  list-style: none;
}

.site-list li {
  padding: 0.3em;
}

.site-list__link {
  position: relative;
  color: @page-black;
  text-decoration: none;
  transition-property: color;
  transition-duration: 0.1s;
  transition-timing-function: linear;

  &:hover {
    color: @page-pink;
  }

  &:active {
    opacity: 0.5;
  }
}

.site-list__link--blog {
  text-decoration: line-through;
}
